import MerchantsList from "../../static/merchants.json"
export default {
	data() {
		return {
			MerchantsList: MerchantsList.data,
			merchantsList: {},
			collected: 0

		}
	},
	onLoad: function(option) { //option为object类型，会序列化上个页面传递的参数
		var id = option.id

		this.merchantsList = this.MerchantsList[id]

	}
}
